body {
    width: 100%;
    margin: 0 0;
    padding: 0 0;
    font-family: "Montserrat", sans-serif;
    background-image: url(https://res.cloudinary.com/davis-cloud/image/upload/v1599341333/background_d7uczd.jpg);
}

hr {
    width: 90%;
    height: 2px;
    margin: 2rem auto;
    box-shadow: 0 0 50px 2px rgba(255, 255, 255, 0.534);
    background-color:rgba(255, 255, 255, 0.527);
}

.top-button {
    position: fixed;
    width: 3.5vw;
    bottom: 11rem;
    right: 5.5rem;
    z-index: 999;
}

a:focus {
    outline: none;
}

.title {
    max-width: 30rem;
    font-size: 2rem;
    background-color: #90818136;
    box-shadow: 0 0 10px 5px #ffffffd7;
    margin: 1rem auto;
    padding: 1rem 1rem;
    color: white;
    text-align: center;
}